import {Injectable} from '@angular/core';
import {Headers, Http, RequestOptions, Response} from '@angular/http';

import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/throw';

import {Address} from './address';
import {ErrorObservable} from 'rxjs/observable/ErrorObservable';
import {AuthenticationService} from '../login/authentication.service';

@Injectable()
export class AddressService {

  _getAllAddresses = '/api/clientAddresses/';

  constructor(private _http: Http) {
  }

  getAllAddresses(id: number): Observable<Address[]> {
    return this._http.get(this._getAllAddresses + id, this.requestBearer())
      .map((response: Response) => response.json().addresses as Address[])
      .do(data => console.log('All: ' + JSON.stringify(data)))
      .catch(this.handleError);
  }

  getBiggestId(): number {
    const clients = JSON.parse(localStorage.getItem('addresses'));
    let biggestId = -1;
    clients.map(function (currentValue, index, array) {
      if (biggestId < currentValue.id) {
        biggestId = currentValue.id;
      }
    });
    return biggestId;
  }

  private handleError(error: Response): ErrorObservable {
    let errorMessage;
    try {
      errorMessage = error.json().errorMessage;
    } catch (e) {
      errorMessage = error.text();
    } finally {
      return Observable.throw(errorMessage || 'Server error');
    }
  }

  private requestBearer(): RequestOptions {
    return new RequestOptions({
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    });
  }
}



