import {Inject, Injectable, LOCALE_ID} from '@angular/core';
import {FormGroup} from '@angular/forms';

@Injectable()
export class ValidationAndLocaleMessagesService {
  public validationMessages = {
    en: {
      'firstName': {
        'required': 'First name is required.',
        'minlength': 'First name must be at least 3 characters long.',
      },
      'lastName': {
        'required': 'Last name is required.',
        'minlength': 'Last name must be at least 3 characters long.',
      },
      'streetName': {
        'required': 'Street name is required.',
        'minlength': 'Street name must be at least 3 characters long.',
      },
      'cityName': {
        'required': 'City name is required.',
        'minlength': 'City name must be at least 3 characters long.',
      },
      'zipCode': {
        'required': 'Zipcode is required.',
        'minlength': 'Zipcode can\'t have less values than 6.',
        'maxlength': 'Zipcode can\'t have more values than 6.',
        'pattern': 'ZipCode value inserted is not valid',
      },
      'username': {
        'required': 'Username is required.',
        'minlength': 'Username must be at least 3 characters long.',
      },
      'password': {
        'required': 'Password is required.',
      },
    },
    pl: {
      'firstName': {
        'required': 'Imi klienta jest wymagane.',
        'minlength': 'Imi klienta musi mie dugo conajmniej 3 znakw.',
      },
      'lastName': {
        'required': 'Nazwisko klienta jest wymagane.',
        'minlength': 'Nazwisko klienta musi mie dugo conajmniej 3 znakw.',
      },
      'streetName': {
        'required': 'Nazwa ulicy jest wymagana.',
        'minlength': 'Nazwa ulicy musi mie dugo conajmniej 3 znakw.',
      },
      'cityName': {
        'required': 'Nazwa miasta jest wymagana.',
        'minlength': 'Nazwa miasta musi mie dugo conajmniej 3 znakw.',
      },
      'zipCode': {
        'required': 'Kod pocztowy jest wymagany.',
        'minlength': 'Kod pocztowy musi mie dokadnie 6 znakw (11-111).',
        'maxlength': 'Kod pocztowy musi mie dokadnie 6 znakw (11-111).',
        'pattern': 'Podany kod pocztowy jest nieprawidowy.',
      },
      'username': {
        'required': 'Nazwa uytkownika jest wymagana.',
        'minlength': 'Nazwa uytkownika musi mie dugo conajmniej 3 znakw.',
      },
      'password': {
        'required': 'Haso jest wymagane.',
      },
    }
  };

  public applicationMessages = {
    en: {
      'addressExists': 'Address already exists!',
      'addressAdded': 'Address was successfully added.',
      'addressNotAdded': 'Address wasn\'t added. Please check if given address does not already exists.',
      'alreadyMainAddress': 'This address is already a main address!',
      'cannotDeleteMainAddress': 'You can\'t delete address that is currently a main address.',
      'removeAddressConfirmTitle': 'Address deletion',
      'removeAddressConfirmMessage': 'Do you want to remove selected address?',

      'clientExists': 'Client already exists!',
      'clientAdded': 'Client was successfully added.',
      'clientNotAdded': 'Client wasn\'t added.',
      'removeClientConfirmTitle': 'Client deletion',
      'removeClientConfirmMessage': 'Do you want to remove selected client?',

      'dataBeingResolved': 'Please wait while data is being resolved.',
      'noMatchForFilter': 'There are no clients that match your filter sentence.',
      'cancelAction': 'Cancel',
      'confirmAction': 'Confirm',
      'rowNotSelected': 'Please select an row.',
      'userHasLogout': 'Logout successful.',
      'tokenHasExpired': 'User token has expired! Please login again.',
      'emptyDatabase': 'Server returned empty array, there are no users in database!',
      'serverOffline': 'Server is offline.',
      'errorTitle': 'Error!',
      'successTitle': 'Success!',

      // additional messages that would have been returned by back-end server
      'clientRemoved': 'Client was successfully deleted.',
      'clientUpdated': 'Client was successfully updated.',
      'mainAddressUpdated' : 'Main address was succesfully updated'
    },
    pl: {
      'addressExists': 'Adres ju istnieje!',
      'addressAdded': 'Adres zosta pomylnie dodany.',
      'addressNotAdded': 'Adres nie zosta dodany. Prosz sprawdzi czy dany adres ju nie istnieje.',
      'alreadyMainAddress': 'Ten adres ju jest domylnym adresem!',
      'cannotDeleteMainAddress': 'Usunicie domylnego adresu jest niemoliwe.',
      'removeAddressConfirmTitle': 'Usunicie adresu',
      'removeAddressConfirmMessage': 'Czy na pewno chcesz usun ten adres?',

      'clientExists': 'Klient ju istnieje!',
      'clientAdded': 'Klient zosta pomylnie dodany.',
      'clientNotAdded': 'Adres nie zosta dodany.',
      'removeClientConfirmTitle': 'Usunicie klienta',
      'removeClientConfirmMessage': 'Czy na pewno chcesz usun tego klienta?',

      'dataBeingResolved': 'Prosz czeka, dane s pobierane w tle.',
      'noMatchForFilter': 'Nie istnieje aden klient speniajcy kryteria wyszukiwania.',
      'cancelAction': 'Anuluj',
      'confirmAction': 'Kontynuuj',
      'rowNotSelected': 'Prosz wybra wiersz.',
      'userHasLogout': 'Nastpio poprawne wylogowanie.',
      'tokenHasExpired': 'Token uytkownika wygas! Prosz zalogowa si ponownie.',
      'emptyDatabase': 'Serwer zwrci pust tablic, baza danych jest pusta!',
      'serverOffline': 'Serwer jest offline.',
      'errorTitle': 'Bd!',
      'successTitle': 'Sukces!'
    }
  };

  constructor(@Inject(LOCALE_ID) public localeId: string) {
  }

  public getLocalizedValidationMessages(field: any) {
    return this.validationMessages[this.localeId][field];
  }

  public getLocalizedMessages(field: string) {
    return this.applicationMessages[this.localeId][field];
  }

  public onValueChanged(form: FormGroup, formErrors: {}, data ?: any) {
    if (!form) {
      return;
    }
    for (const field in formErrors) {
      if (formErrors.hasOwnProperty(field)) {
        // check field
        formErrors[field] = '';
        const control = form.get(field);
        if (control && control.dirty && !control.valid) {
          const messages = this.getLocalizedValidationMessages(field);
          for (const key in control.errors) {
            if (control.errors.hasOwnProperty(key)) {
              formErrors[field] += messages[key] + ' ';
            }
          }
        }
      }
    }
  }
}



