import {Injectable} from '@angular/core';
import {Headers, Http, RequestOptions, Response} from '@angular/http';

import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/throw';
import "rxjs/add/observable/of";

import {Client} from './client';
import {ErrorObservable} from 'rxjs/observable/ErrorObservable';

@Injectable()
export class ClientService {

  _getAllClients = '/api/clients';

  constructor(private _http: Http) {
  }

  getAllClients(): Observable<Client[]> {
    return this._http.get(this._getAllClients)
      .map((response: Response) => response.json() as Client[])
      .catch(this.handleError);
  }

  getClient(clientId: number): Observable<Client> {
    const clients = JSON.parse(localStorage.getItem('clients'));
    const wantedClient = clients.find(client => client.id === clientId);
    return Observable.of(wantedClient);
  }

  getBiggestId(): number {
    const clients = JSON.parse(localStorage.getItem('clients'));
    let biggestId = -1;
    clients.map(function (currentValue, index, array) {
      if (biggestId < currentValue.id) {
        biggestId = currentValue.id;
      }
    });
    return biggestId;
  }

  private handleError(error: Response): ErrorObservable {
    let errorMessage;
    try {
      errorMessage = error.json().errorMessage;
    } catch (e) {
      errorMessage = error.text();
    } finally {
      return Observable.throw(errorMessage || 'Server error');
    }
  }

  private requestBearer(): RequestOptions {
    return new RequestOptions({
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    });
  }
}



